  position: relative;
  z-index: 1;
  cursor: pointer; }
  .selectbox_input_label {
    font-weight: bold; }

.selectbox_list {
  display: none;
  position: absolute;
  left: 0;
  top: calc(100% + 15px);
  list-style-type: none;
  margin: 0;
  padding: 10px 0;
  width: calc(100% - 2px);
  overflow-y: auto;
  max-height: 160px;
  min-width: 80px;
  z-index: 2; }
  .selectbox_list_item {
    padding: 8px 15px;
    cursor: pointer; }
    .selectbox_list_item:last-child {
      border-bottom-width: 0; }
    .selectbox_list_item:hover {
      background-color: var(--color-secondary-6);
      color: var(--color-black-2); }
    .selectbox_list_item.selected {
      background-color: var(--color-secondary-8);
      color: var(--color-white); }
    .selectbox_list_item.disabled {
      opacity: 0.5;
      cursor: not-allowed; }
      .selectbox_list_item.disabled:hover {
        background-color: white; }

.selectbox_open .selectbox_list {
  display: block; }

.selectbox_open .selectbox_input [data-js-select-arrow] {
  transform: rotate(-180deg); }

.selectbox_direction_bottom .selectbox_list {
  top: inherit;
  bottom: 100%; }

